@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 18px;
  font-family: "Oswald", "Open Sans", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  margin: 0;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@keyframes top {
  0% {
    top: 0;
    transform: rotate(0);
  }
  50% {
    top: 5px;
    transform: rotate(0);
  }
  100% {
    top: 5px;
    transform: rotate(45deg);
  }
}
@keyframes top-2 {
  0% {
    top: 5px;
    transform: rotate(45deg);
  }
  50% {
    top: 5px;
    transform: rotate(0deg);
  }
  100% {
    top: -3px;
    transform: rotate(0deg);
  }
}
@keyframes bottom {
  0% {
    bottom: 0;
    transform: rotate(0);
  }
  50% {
    bottom: 5px;
    transform: rotate(0);
  }
  100% {
    bottom: 5px;
    transform: rotate(135deg);
  }
}
@keyframes bottom-2 {
  0% {
    bottom: 5px;
    transform: rotate(135deg);
  }
  50% {
    bottom: 5px;
    transform: rotate(0);
  }
  100% {
    bottom: -3px;
    transform: rotate(0);
  }
}
@keyframes scaled {
  50% {
    transform: scale(0);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes scaled-2 {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    padding: 0 110px;
  }
}

@media (min-width: 768px) and (max-width: 1280px) {
  .form-container .container {
    padding: 0 70px;
  }
}

@media (min-width: 1280px) {
  .address .container {
    pointer-events: none;
  }
}

.page-header {
  position: relative;
  z-index: 1;
  border-bottom: 1px solid #d9d9d9;
}
.page-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-header::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header {
    padding: 0;
    border-bottom: none;
  }
}
@media (min-width: 1280px) {
  .page-header {
    padding: 53px 0 29px 0;
  }
}

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.page-header__link {
  z-index: 5;
  display: block;
  padding: 12px 0 16px 0;
}
.page-header__link:focus {
  border-radius: 10px;
  outline: 1px solid rgba(104, 183, 56, 0.6);
  outline-offset: 5px;
}
.page-header__link:focus .page-header__logo {
  opacity: 0.8;
}
@media (min-width: 768px) {
  .page-header__link {
    padding: 0;
  }
}

.page-header__logo {
  display: block;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1280px) {
  .page-header__logo {
    width: 202px;
    height: 59px;
  }
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}

.page-intro {
  position: relative;
  z-index: 1;
  overflow: hidden;
}
@media (min-width: 1280px) {
  .page-intro::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -2;
    width: 50%;
    height: 100%;
    background-image: url("../img/decor/kitty-big.jpg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .page-intro::before {
    background-image: url("../img/decor/kitty-big@2x.jpg");
  }
}
@media (min-width: 1280px) {
  .page-intro::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -2;
    width: 50%;
    height: 100%;
    background-color: rgba(104, 183, 56, 0.85);
  }
}

.navigation {
  display: flex;
  align-items: center;
}

.navigation__toggle {
  right: 25px;
  z-index: 5;
  width: 24px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation__toggle-elem {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000000;
  transition: all 0.3s;
}
.navigation__toggle-elem + .navigation__toggle-elem {
  margin: 3px 0 0 0;
}

.navigation__toggle--active .navigation__toggle-elem:nth-child(1) {
  animation: ease 0.5s top forwards;
}

.navigation__toggle--not-active .navigation__toggle-elem:nth-child(1) {
  animation: ease 0.5s top-2 forwards;
}

.navigation__toggle--active .navigation__toggle-elem:nth-child(2) {
  animation: ease 0.5s scaled forwards;
}

.navigation__toggle--not-active .navigation__toggle-elem:nth-child(2) {
  animation: ease 0.5s scaled-2 forwards;
}

.navigation__toggle--active .navigation__toggle-elem:nth-child(3) {
  animation: ease 0.5s bottom forwards;
}

.navigation__toggle--not-active .navigation__toggle-elem:nth-child(3) {
  animation: ease 0.5s bottom-2 forwards;
}

.navigation__list {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: -700px;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  list-style-type: none;
  text-align: center;
  transition: top 0.5s;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    z-index: 5;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-self: flex-end;
    min-width: 440px;
    max-width: 500px;
    height: auto;
  }
}
@media (min-width: 1280px) {
  .navigation__list {
    min-width: 478px;
  }
}

@media (max-width: 768px) {
  .navigation--no-js {
    z-index: 5;
    width: 100%;
  }
  .navigation--no-js .navigation__toggle {
    display: none;
  }
  .navigation--no-js .navigation__list {
    position: static;
  }
  .navigation--no-js .navigation__item {
    position: static;
  }
}

.navigation__list--active {
  top: 66px;
}

.navigation__item {
  width: 100%;
  background-color: #ffffff;
}
.navigation__item:first-child .navigation__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 10;
  width: 200vw;
  height: 1px;
  background-color: #eaeaea;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .navigation__item:first-child .navigation__link::before {
    display: none;
  }
}
.navigation__item .navigation__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  z-index: 10;
  width: 200vw;
  height: 1px;
  background-color: #eaeaea;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .navigation__item .navigation__link::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation__item {
    position: static;
    width: auto;
    height: auto;
    padding: 0;
    background-color: rgba(255, 255, 255, 0);
    transition: none;
  }
}

.navigation__link {
  position: relative;
  display: block;
  margin: 0;
  padding: 22px 10px 24px 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 17px 0 10px 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1280px) {
  .navigation__link {
    font-size: 20px;
    line-height: 30px;
  }
}
.navigation__link:hover, .navigation__link:focus {
  outline: none;
  opacity: 0.6;
}
.navigation__link:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .navigation__link--current::after {
    content: "";
    position: absolute;
    bottom: 2px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}

@media (min-width: 1280px) {
  .page-intro .navigation__link {
    color: #ffffff;
  }
  .page-intro .navigation__link::after {
    background-color: #ffffff;
  }
}

