.hero__heading::before {
  content: "";
  position: absolute;
  top: -27px;
  left: -20px;
  z-index: -1;
  width: calc(100% + 40px);
  height: calc(100% + 162px);
  background-image: image-set(url("../img/decor/kitty.webp") 1x, url("../img/decor/kitty.jpg") 1x);
  background-repeat: no-repeat;
  background-position: center;
